@use'base/variables' as v;
@use'base/mixins' as m;

.header{
 // background-color: #424242;
  
  @include m.tablet { 

  }
  &__barra{
    padding: 0 2rem;
    display: flex;
    justify-content: flex-start;
    @include m.tablet { 
      padding: 0;
      justify-content: space-between;
      
    }
  }
  &__logo{
    width: 25rem;
    @include m.tablet { 
      width: 25rem;
    }
  }
  &__button{
    background: transparent;
    border: none;
    cursor: pointer;
    top: 15px;
    right: 30px;
    z-index: 2;
    @include m.tablet { 
      display: none;
    }
  }
}
.button{
  background: transparent;
  border: none;
  position: absolute;
  cursor: pointer;
  top: 30px;
  right: 40px;
  z-index: 2;
}

.navegacion{
  
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  background: #2f3640;
  width: 50%;
  height: 100%;
  opacity: 0.9;
  padding: 30px;
  transform: translateX(-500px);
  transition: transform 0.5s ease-in-out;
  @include m.tablet {
  display: flex;
  position: relative;
  align-items: center;
  color: #607c8a;
  padding: 0;
  gap: 2rem;
  background-color: inherit;
  flex-direction: row;
  transform: translateX(0px);
  width: auto;
  height: auto;
  top: 0;
  }
  @include m.desktop { 
    align-items: flex-end;
  }
  &.show{
    transform: translateX(0px);
    
  }
  &__link{
    color: v.$blanco;
    font-size: 1.8rem;
    margin-bottom: 4rem;
    display: block;
    @include m.tablet { 
      padding:  0.5rem ;
      border-radius: 2rem;
      margin-bottom: 1rem;
    }
    @include m.desktop { 
      margin-bottom: 4rem;
    }
    &:hover{
      color: white;
      padding: 0.5rem;
      border: solid 2px #4cae4f;
      background-color: #4cae4f;
      border-radius: 2rem;
      transform: scale(1.05);
      transition-duration: 0.7s;
    }
    @include m.tablet { 
      color: #2f3640;
    }

  }
}

svg{
  padding-bottom: 2px;
  vertical-align: middle;
  @include m.tablet { 
    display: none;
  }

}

.teclado{
  
  font-style: normal;
}