@use'base/variables' as v;
@use'base/mixins' as m;

.agregado{
  margin-top: -35rem;
  background-image: linear-gradient(to bottom, transparent 0%, transparent 50%, v.$primario 50%, v.$primario 100%), url(../img/portada.jpeg); 
  background-size: 100%, 100%;
  background-position: center, 0rem 40rem;
  background-repeat: no-repeat;
  @include m.telefono{
    background-position: center, 0rem -2rem;
    @include m.tablet{
      margin-top: 5rem;
      background-image: linear-gradient(to right, transparent 0%, transparent 50%, v.$primario 50%, v.$primario 100%), url(../img/portada.jpeg); 
      background-position: center, -30rem 1rem;
      background-size: 100%, 190%;
      
      @include m.desktop{
        background-size: 100%, 150%;
        background-position: center, -20rem -10rem;
        @include m.desktopXL { 
          background-size: 100%, 100%;
        }
      }
      
    }
    
  }

  &__grid{
    display: grid;
    grid-template-rows: repeat(2,1fr);
    @include m.tablet{
      grid-template-rows: unset;
      @include m.grid(2, 2rem);
    }
  }

  &__contenido{
    padding: 5rem;
    grid-row: 2/3;
    @include m.tablet{
      padding: 10rem 5rem;
      grid-column: 2/3;
      @include m.desktop{
        padding: 12rem 6rem;
      }
    }
  }

  &__heading{
  color: v.$blanco;    
  text-transform: uppercase;
  }

  &__texto{
    color: v.$blanco;
    font-size: 1.8rem;
    line-height: 1.7;
    padding: 1rem 0;
  }

  &__enlace{
    margin-top: 5rem;
    @include m.boton(v.$blanco, v.$primario);
    display: block;
  }
}