@use 'variables' as v; 

html {
    font-size: 62.5%;
    box-sizing: border-box;
}
*, *:before, *:after {
    box-sizing: inherit;
}
  
body {
    font-family: v.$fuentePrincipal;
    font-size: 1.6rem;
    background-color: darken(v.$grisClaro, 3%);
}
p {
  font-family:v.$fuentePrincipal;
  color: v.$negro;
  font-size: 2rem;
}
.contenedor {
  width: 95%;
  max-width: 1100px;
  margin: 0 auto;
}

a {
  text-decoration: none;
  font-size: 1.9rem;
}

img {
  max-width: 100%;
  display: block;
}

h1, h2, h3 {
  margin: 0  0 ;
  
}
h1 {
  font-size: 7rem;
  font-family: v.$fuenteTitulo;
  font-weight: 700;
}
h2 {
  font-family: v.$fuenteSecundaria;
  font-size: 3.6rem;
}
h3 {
  font-size: 3.2rem;
}
ul {
    padding:0;
    list-style: none;
    margin: 0;
}