@use'base/variables' as v;
@use'base/mixins' as m;

.calidad{
  margin: 10rem auto;
  
  
  &__heading{
    @include m.heading;
    
  }

  &__titulo{
    line-height: 1.5;
    text-align: justify;
    padding: 0 2rem;
    @include m.tablet { 
      padding: 1rem 10rem 5rem 10rem;
    }
      
  }
  &__imagen{
    width: 30rem;
    margin: 0 auto
  }

  &__grid{
    @include m.grid(1, 2rem);
    @include m.tablet { 
      @include m.grid(3, 5rem);
    }
  }

}

.principio{
  line-height: 1.3;
  background-color: v.$grisClaro;
  display: flex;
  flex-direction: column;
  text-align: center;
  padding: 0 2rem;
  border: solid 4px #4cae4f;
  border-radius: 1rem;
  color: transparent;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;

  &:hover{
    background-color: green;
    transform: scale(1.1);
    transition-duration: .3s;
    transform: scaleX(2deg);
    color: white;
    transition: transform 0.5s ease-in-out;
    p{
      color: white;
    }
  }
  p{
    color: transparent;
  }
  &:nth-child(1){
    background-image: url(../img/politicas1.jpeg);
    &:hover{
      background-image:  linear-gradient(to right, transparentize(#4cae4f, .1) 50%, transparentize(#4cae4f, .1) 100%),url(../img/politicas1.jpeg);

    }
  }

  &:nth-child(2){
    background-image: url(../img/politicas2.jpeg);
    &:hover{
      background-image:  linear-gradient(to right, transparentize(#4cae4f, .1) 50%, transparentize(#4cae4f, .1) 100%),url(../img/politicas2.jpeg);

    }
  }

  &:nth-child(3){
    background-image: url(../img/politicas3.jpeg);
    &:hover{
      background-image:  linear-gradient(to right, transparentize(#4cae4f, .1) 50%, transparentize(#4cae4f, .1) 100%),url(../img/politicas3.jpeg);

    }
  }

}