@use'base/variables' as v;
@use'base/mixins' as m;

.swiper{
  width: 30rem;
  height: 30rem;
  @include m.tablet { 
    width: 40rem;
    height: 40rem;
  }
  &__contenedor{
    position: relative;
    height: 40rem;
    h2{
      text-align: center;
      color: v.$primario;
      font-weight: 900;
    }
  }
  
  
}

.swiper-slide{
  text-align: center;
  font-size: 18px;
  background: #F0F0F0;
  display: flex;
  justify-content: center;
  align-items: center;
  img{
    display: block;
    width: 50rem;
    height:40rem;
    object-fit: scale-down;
  }
}