html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Cantarell", sans-serif;
  font-size: 1.6rem;
  background-color: #f0f0f0;
}

p {
  font-family: "Cantarell", sans-serif;
  color: #000;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 1100px;
  margin: 0 auto;
}

a {
  text-decoration: none;
  font-size: 1.9rem;
}

img {
  max-width: 100%;
  display: block;
}

h1, h2, h3 {
  margin: 0 0;
}

h1 {
  font-size: 7rem;
  font-family: "Yrsa";
  font-weight: 700;
}

h2 {
  font-family: "Lusitana";
  font-size: 3.6rem;
}

h3 {
  font-size: 3.2rem;
}

ul {
  padding: 0;
  list-style: none;
  margin: 0;
}

.text-center {
  text-align: center;
}

.bg-primario {
  background-color: #4cae4f;
}

/** Media Queries **/
.header__barra {
  padding: 0 2rem;
  display: flex;
  justify-content: flex-start;
}
@media (min-width: 768px) {
  .header__barra {
    padding: 0;
    justify-content: space-between;
  }
}
.header__logo {
  width: 25rem;
}
@media (min-width: 768px) {
  .header__logo {
    width: 25rem;
  }
}
.header__button {
  background: transparent;
  border: none;
  cursor: pointer;
  top: 15px;
  right: 30px;
  z-index: 2;
}
@media (min-width: 768px) {
  .header__button {
    display: none;
  }
}

.button {
  background: transparent;
  border: none;
  position: absolute;
  cursor: pointer;
  top: 30px;
  right: 40px;
  z-index: 2;
}

.navegacion {
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  background: #2f3640;
  width: 50%;
  height: 100%;
  opacity: 0.9;
  padding: 30px;
  transform: translateX(-500px);
  transition: transform 0.5s ease-in-out;
}
@media (min-width: 768px) {
  .navegacion {
    display: flex;
    position: relative;
    align-items: center;
    color: #607c8a;
    padding: 0;
    gap: 2rem;
    background-color: inherit;
    flex-direction: row;
    transform: translateX(0px);
    width: auto;
    height: auto;
    top: 0;
  }
}
@media (min-width: 1024px) {
  .navegacion {
    align-items: flex-end;
  }
}
.navegacion.show {
  transform: translateX(0px);
}
.navegacion__link {
  color: #FFFFFF;
  font-size: 1.8rem;
  margin-bottom: 4rem;
  display: block;
}
@media (min-width: 768px) {
  .navegacion__link {
    padding: 0.5rem;
    border-radius: 2rem;
    margin-bottom: 1rem;
  }
}
@media (min-width: 1024px) {
  .navegacion__link {
    margin-bottom: 4rem;
  }
}
.navegacion__link:hover {
  color: white;
  padding: 0.5rem;
  border: solid 2px #4cae4f;
  background-color: #4cae4f;
  border-radius: 2rem;
  transform: scale(1.05);
  transition-duration: 0.7s;
}
@media (min-width: 768px) {
  .navegacion__link {
    color: #2f3640;
  }
}

svg {
  padding-bottom: 2px;
  vertical-align: middle;
}
@media (min-width: 768px) {
  svg {
    display: none;
  }
}

.teclado {
  font-style: normal;
}

.slider {
  width: 100%;
  overflow: hidden;
}
.slider__contenedor {
  width: 100%;
  display: flex;
}
.slider__contenido {
  width: 100%;
  height: 500px;
  display: flex;
  flex-direction: column-reverse;
  justify-content: space-around;
  align-items: flex-end;
  flex-shrink: 0;
}
.slider__contenido:nth-child(1) {
  background-image: linear-gradient(to right, rgba(66, 66, 66, 0.1) 10%, rgba(66, 66, 66, 0.9) 70%), url(../img/publicidad1.jpeg);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: top center;
}
.slider__contenido:nth-child(2) {
  background-image: linear-gradient(to left, rgba(76, 174, 79, 0.8) 0%, rgba(76, 174, 79, 0.2) 100%), url(../img/portada.jpeg);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
}
.slider__contenido:nth-child(3) {
  background: #611a68;
  color: #fff;
}
.slider__contenido:nth-child(4) {
  background-image: linear-gradient(to left, rgba(76, 174, 79, 0.9) 0%, rgba(76, 174, 79, 0.3) 100%), url(../img/portada.jpeg);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
}
@media (min-width: 768px) {
  .slider__contenido {
    flex-direction: row-reverse;
    height: 650px;
    align-items: center;
  }
}
.slider__contenido > img {
  width: 300px;
}
@media (min-width: 768px) {
  .slider__contenido > img {
    width: 400px;
  }
}
@media (min-width: 1024px) {
  .slider__contenido > img {
    width: 500px;
  }
}
.slider__contenido > div {
  width: 80%;
}
@media (min-width: 768px) {
  .slider__contenido > div {
    width: 40%;
  }
}
.slider__contenido__text-position {
  display: flex;
}
.slider__contenido a {
  font-size: 1.5rem;
  color: #fff;
  background-color: #4cae4f;
  width: 60%;
  display: block;
  padding: 15px 0;
  text-align: center;
  border-radius: 3px;
  margin-top: 20px;
  text-decoration: none;
}
.slider__contenido a:nth-child(2) {
  background: #fff;
  color: #232863;
}
@media (min-width: 768px) {
  .slider__contenido a {
    width: 50%;
  }
}

.texto {
  font-size: 3rem;
  font-weight: 900;
  line-height: 30px;
  color: white;
  margin-bottom: 1rem;
}
@media (min-width: 768px) {
  .texto {
    display: 1.5rem;
    text-align: justify;
    margin-top: 20rem;
  }
}
.texto__span {
  font-size: 2rem;
  color: white;
  text-align: center;
}

.calidad {
  margin: 10rem auto;
}
.calidad__heading {
  font-weight: 400;
}
.calidad__heading span {
  font-weight: 700;
}
.calidad__heading::after {
  content: "";
  display: block;
  height: 1rem;
  width: 15rem;
  margin-top: 1rem;
  background-image: linear-gradient(to right, #4cae4f 0%, #4cae4f 50%, #91cf93 51%, #91cf93 100%);
}
.calidad__titulo {
  line-height: 1.5;
  text-align: justify;
  padding: 0 2rem;
}
@media (min-width: 768px) {
  .calidad__titulo {
    padding: 1rem 10rem 5rem 10rem;
  }
}
.calidad__imagen {
  width: 30rem;
  margin: 0 auto;
}
.calidad__grid {
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(1, 1fr);
}
@media (min-width: 768px) {
  .calidad__grid {
    display: grid;
    gap: 5rem;
    grid-template-columns: repeat(3, 1fr);
  }
}

.principio {
  line-height: 1.3;
  background-color: #f8f8f8;
  display: flex;
  flex-direction: column;
  text-align: center;
  padding: 0 2rem;
  border: solid 4px #4cae4f;
  border-radius: 1rem;
  color: transparent;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
}
.principio:hover {
  background-color: green;
  transform: scale(1.1);
  transition-duration: 0.3s;
  transform: scaleX(2deg);
  color: white;
  transition: transform 0.5s ease-in-out;
}
.principio:hover p {
  color: white;
}
.principio p {
  color: transparent;
}
.principio:nth-child(1) {
  background-image: url(../img/politicas1.jpeg);
}
.principio:nth-child(1):hover {
  background-image: linear-gradient(to right, rgba(76, 174, 79, 0.9) 50%, rgba(76, 174, 79, 0.9) 100%), url(../img/politicas1.jpeg);
}
.principio:nth-child(2) {
  background-image: url(../img/politicas2.jpeg);
}
.principio:nth-child(2):hover {
  background-image: linear-gradient(to right, rgba(76, 174, 79, 0.9) 50%, rgba(76, 174, 79, 0.9) 100%), url(../img/politicas2.jpeg);
}
.principio:nth-child(3) {
  background-image: url(../img/politicas3.jpeg);
}
.principio:nth-child(3):hover {
  background-image: linear-gradient(to right, rgba(76, 174, 79, 0.9) 50%, rgba(76, 174, 79, 0.9) 100%), url(../img/politicas3.jpeg);
}

.agregado {
  margin-top: -35rem;
  background-image: linear-gradient(to bottom, transparent 0%, transparent 50%, #4cae4f 50%, #4cae4f 100%), url(../img/portada.jpeg);
  background-size: 100%, 100%;
  background-position: center, 0rem 40rem;
  background-repeat: no-repeat;
}
@media (min-width: 480px) {
  .agregado {
    background-position: center, 0rem -2rem;
  }
}
@media (min-width: 480px) and (min-width: 768px) {
  .agregado {
    margin-top: 5rem;
    background-image: linear-gradient(to right, transparent 0%, transparent 50%, #4cae4f 50%, #4cae4f 100%), url(../img/portada.jpeg);
    background-position: center, -30rem 1rem;
    background-size: 100%, 190%;
  }
}
@media (min-width: 480px) and (min-width: 768px) and (min-width: 1024px) {
  .agregado {
    background-size: 100%, 150%;
    background-position: center, -20rem -10rem;
  }
}
@media (min-width: 480px) and (min-width: 768px) and (min-width: 1024px) and (min-width: 1200px) {
  .agregado {
    background-size: 100%, 100%;
  }
}
.agregado__grid {
  display: grid;
  grid-template-rows: repeat(2, 1fr);
}
@media (min-width: 768px) {
  .agregado__grid {
    grid-template-rows: unset;
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
.agregado__contenido {
  padding: 5rem;
  grid-row: 2/3;
}
@media (min-width: 768px) {
  .agregado__contenido {
    padding: 10rem 5rem;
    grid-column: 2/3;
  }
}
@media (min-width: 768px) and (min-width: 1024px) {
  .agregado__contenido {
    padding: 12rem 6rem;
  }
}
.agregado__heading {
  color: #FFFFFF;
  text-transform: uppercase;
}
.agregado__texto {
  color: #FFFFFF;
  font-size: 1.8rem;
  line-height: 1.7;
  padding: 1rem 0;
}
.agregado__enlace {
  margin-top: 5rem;
  background-color: #FFFFFF;
  padding: 1.5rem 4rem;
  color: #4cae4f;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
}
@media (min-width: 768px) {
  .agregado__enlace {
    width: auto;
  }
}
.agregado__enlace:hover {
  background-color: #e6e6e6;
}

.footer {
  padding: 5rem 0;
  background-color: #424242;
}
.footer__titulo {
  color: #FFFFFF;
  margin: 3rem 0;
  font-size: 2.5rem;
}
.footer__navegacion {
  display: flex;
  flex-direction: column;
}
.footer__navegacion__link {
  display: inline;
  color: #FFFFFF;
  padding: 1rem;
  font-size: 1.5rem;
}
.footer__navegacion__link:hover {
  transform: scale(1.1);
  transition-duration: 0.5s;
}
.footer__grid {
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(1, 1fr);
}
@media (min-width: 768px) {
  .footer__grid {
    display: grid;
    gap: 5rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.footer__texto {
  line-height: 1.5;
  color: #FFFFFF;
  text-align: justify;
  margin: 2rem 0;
  font-size: 1.5rem;
  padding-right: 5rem;
}
.footer__imagen {
  width: 25rem;
}

.swiper {
  width: 30rem;
  height: 30rem;
}
@media (min-width: 768px) {
  .swiper {
    width: 40rem;
    height: 40rem;
  }
}
.swiper__contenedor {
  position: relative;
  height: 40rem;
}
.swiper__contenedor h2 {
  text-align: center;
  color: #4cae4f;
  font-weight: 900;
}

.swiper-slide {
  text-align: center;
  font-size: 18px;
  background: #F0F0F0;
  display: flex;
  justify-content: center;
  align-items: center;
}
.swiper-slide img {
  display: block;
  width: 50rem;
  height: 40rem;
  object-fit: scale-down;
}