@use'base/variables' as v;
@use'base/mixins' as m;

.footer{
  padding: 5rem 0;
  background-color: #424242;

  &__titulo{
    color: v.$blanco;
    margin: 3rem 0;
    font-size: 2.5rem;
  }
  &__navegacion{
    display: flex;
    flex-direction: column;
    
    &__link{
      display: inline;
      color: v.$blanco;
      padding: 1rem ;
      font-size: 1.5rem;
      &:hover{
        transform: scale(1.1);
        transition-duration: 0.5s;
      }
      
    }
  }
  &__grid{
    @include m.grid(1, 2rem);
    @include m.tablet { 
      @include m.grid(3, 5rem);
    }
  }

  &__texto{
    line-height: 1.5;
    color: v.$blanco;
    text-align: justify;
    margin: 2rem 0;
    font-size: 1.5rem;
    padding-right: 5rem;
  }

  &__imagen{
    width: 25rem;
  }
}