@use'base/variables' as v;
@use'base/mixins' as m;

.slider{
  width: 100%;
  overflow: hidden;

  &__contenedor{
    width: 100%;
    display: flex;
  }

  &__contenido{
    width: 100%;
    height: 500px;
    display: flex;
    flex-direction: column-reverse;
    justify-content: space-around;
    align-items: flex-end;
    flex-shrink: 0;
  
    &:nth-child(1){
      background-image:  linear-gradient(to right, transparentize(#424242, .9) 10%, transparentize(#424242, .1) 70%),url(../img/publicidad1.jpeg);
      background-size: cover;
      background-repeat: no-repeat;
      background-position: top center;
    }
    &:nth-child(2){
      background-image:  linear-gradient(to left, transparentize(v.$primario, .2) 0%, transparentize(v.$primario, .8) 100%),url(../img/portada.jpeg);
      background-size: cover;
      background-repeat: no-repeat;
      background-position: center ;
    }
    &:nth-child(3){
      background: #611a68;
      color: #fff;
    }
    &:nth-child(4){
      background-image:  linear-gradient(to left, transparentize(v.$primario, .1) 0%, transparentize(v.$primario, .7) 100%),url(../img/portada.jpeg);
      background-size: cover;
      background-repeat: no-repeat;
      background-position: center ;
    }
  
    @include m.tablet { 
      flex-direction: row-reverse;
      height: 650px;
      align-items: center;
    }
    >img{
      width: 300px;
      @include m.tablet { 
        width: 400px;
      }
      @include m.desktop { 
        width: 500px;
      }
    }
    >div{
      width: 80%;
      @include m.tablet { 
        width: 40%;
      }
    }
    &__text-position{
      display: flex;
    }


    a{
      font-size: 1.5rem;
      color:#fff;
      background-color: #4cae4f;
      width: 60%;
      display: block;
      padding: 15px 0;
      text-align: center;
      border-radius: 3px;
      margin-top: 20px;
      text-decoration: none;

      &:nth-child(2){
        background: #fff;
        color: #232863;
      }
      @include m.tablet { 
        width: 50%;
      }
    }
  }
}

.texto{
  font-size: 3rem;
  font-weight: 900;
  line-height: 30px;
  color: white;
  margin-bottom: 1rem;
  @include m.tablet { 
    display: 1.5rem;
    text-align: justify;
    margin-top: 20rem;
  }
  &__span{
    font-size: 2rem;
    color: white;
    text-align: center;
  }
}